import React from 'react'
import { FaTrash } from 'react-icons/fa';

import Container from '../components/Container'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Circle from '../components/Circle'
import Text from '../components/Text'
import Button from '../components/Button'
import FullpageVerticalCenter from '../components/FullpageVerticalCenter'
import { responsive } from '../components/ThemeProvider/theme';

import useResponsive from '../contexts/mediaQuery/useResponsive'

import useData from './TrashPage/data/useData'
import useShowHeader from '../contexts/header/useShowHeader';

const HomePage = () => {
  const { isMobile } = useResponsive()
  const data = useData()
  useShowHeader()
  return (
    <FullpageVerticalCenter>
      <Container py={responsive(0, '2em')} textAlign="center">
        <Flex alignItems="center" justifyContent="center">
          <Text color={isMobile ? 'danger' : 'black'} fontSize={responsive('1em', '1.5em')}>
            首頁會有垃圾山
          </Text>
          <Circle width="2em" bg="primary" color="white" display="inline-block" mx="1em" verticalAlign="middle">
            <FaTrash />
          </Circle>
        </Flex>
        {data && (
          <Box mx="-1em" my="8">
            {data.map((d) => d.parts && (
              <Button key={d.name} m="0.5em" to={`/trash/${d.id}`}>{d.name}</Button>
            ))}
          </Box>
        )}
      </Container>
    </FullpageVerticalCenter>
  )
}

export default HomePage
