import React, { useEffect, useRef, useState, useMemo, createRef } from 'react'
import { AspectRatio } from '@chakra-ui/react'
import { range } from 'lodash'
import gsap, { TimelineMax, TweenMax } from 'gsap'
import ScrollMagic from "scrollmagic";
import { ScrollMagicPluginGsap } from "scrollmagic-plugin-gsap";

import FullpageHeight from '../../components/FullpageHeight'
import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Button from '../../components/Button'
import Circle from '../../components/Circle'
import Container from '../../components/Container'
import BackgroundImage from '../../components/BackgroundImage'
import withData from './data/withData';
import animations from './data/animations'
import ChevDown from './ChevDown';
import Hashtag from './Hashtag';
import RateCircle from './RateCircle';

import shareBg from './share-bg.svg'
import Handling from './Handling';
import ScrollIndicator from './ScrollIndicator';

ScrollMagicPluginGsap(ScrollMagic, TweenMax, TimelineMax);

const pageCount = 5
const imgSize = [850, 624]
const getNameSize = name => {
  if (!name) return null
  const { length } = name
  if (length > 5) return '10em'
  return '12em'
}

let explodeTimeline
let partsTimeline
let controller

const TrashName = ({ children, ...props }) => (
  <Box.Absolute
    top="1em"
    left="1em"
    color="white"
    {...props}
  >
    <Text fontSize="2.5em" fontWeight="900" letterSpacing="0.1em">{children}</Text>
  </Box.Absolute>
)

const SectionTitle = ({ children, ...props }) => (
  <Box.Absolute left="1em" top="4.5em" {...props}>
    <Text fontSize="1.5em">{children}</Text>
  </Box.Absolute>
)

const TrashNumber = ({ children, ...props }) => (
  <Box.Absolute
    bottom="2em"
    left="1em"
    color="white"
    {...props}
  >
    <Text.Number fontSize="1.75em">{children}</Text.Number>
  </Box.Absolute>
)

const TrashPage = ({ data }) => {
  const [pinEnd, setPinEnd] = useState()
  const [scrollProgress, setProgress] = useState()
  const trashContainerRef = useRef()
  const trashRef = useRef()
  const partsPageRef = useRef()
  const layerRefs = useMemo(() => data.imgs.map(() => createRef()), [data])
  const animaRefs = useMemo(() => data.imgs.map(() => createRef()), [data])
  const partsRefs = useMemo(() => data.imgs.map(() => createRef()), [data])
  const pagesRef = useMemo(() => range(pageCount).map(() => createRef()), [])
  const colorScheme = 'colors.pink'
  useEffect(() => {
    if (explodeTimeline) {
      explodeTimeline.kill()
    }
    if (partsTimeline) {
      partsTimeline.kill()
    }
    if (controller) {
      controller.destroy()
    }
    controller = new ScrollMagic.Controller();
    // gsap.set(trashRef.current, { width: '80%' })
    new ScrollMagic.Scene({
      triggerElement: pagesRef[0].current,
      duration: `${pageCount * 100 - 50}%`,
    }).addTo(controller)
      .on('progress', (e) => setProgress(e.progress));
    new ScrollMagic.Scene({
      triggerElement: pagesRef[0].current,
      duration: `${pageCount * 100 - 50}%`,
      // triggerHook: 'onEnter',
    }).setPin(trashContainerRef.current, { pushFollowers: false })
      .addTo(controller)
      .on('progress', (e) => setPinEnd(e.progress === 1));
    const animation = animations[data.name]
    explodeTimeline = new TimelineMax()
    partsTimeline = new TimelineMax()
    explodeTimeline.to(trashRef.current, { width: '50%', duration: 1 })
    const yStart = data.totalHeight - (data.totalHeight - imgSize[1]) / 2
    const rateEles = []

    data.imgs.forEach((cfg) => {
      gsap.set(layerRefs[cfg.index].current, { y: '0%' })
      explodeTimeline.to(layerRefs[cfg.index].current, {
        y: `${(yStart - cfg.y - data.positions[cfg.order]) / imgSize[1] * 100}%`,
        duration: 1,
      }, 0)
      if (animation && animation[cfg.layerName]) {
        Object.entries(animation[cfg.layerName]).forEach(([d, ani]) => {
          explodeTimeline.to(animaRefs[cfg.index].current, {
            ...ani,
            duration: 1 * (d / 100),
          }, 0)
        })
      }
      if (cfg.partName) {
        explodeTimeline.to(partsRefs[cfg.index].current.querySelector('.circle-container'), {
          [cfg.side ? 'left' : 'right']: '100%',
          duration: 1,
        }, 0)
        explodeTimeline.to(partsRefs[cfg.index].current.querySelector('.line'), {
          [cfg.side ? 'right' : 'left']: '-2%',
          opacity: 1,
          duration: 1,
        }, 0)

        explodeTimeline.to(partsRefs[cfg.index].current.querySelector('.circle-1'), {
          opacity: 1,
          duration: 1 / 2,
        }, 1 / 2)
        // partsTimeline.to(partsRefs[cfg.index].current.querySelector('.circle-1'), {
        //   opacity: 0,
        //   duration: 1 / 2,
        // }, 0)
        partsTimeline.to(partsRefs[cfg.index].current.querySelector('.circle-2'), {
          scale: 1,
          duration: 1,
        }, 0)
        partsTimeline.to(partsRefs[cfg.index].current.querySelector('.circle-rate'), {
          opacity: 1,
          duration: 1 / 2,
        }, 1 / 2)
        const rateEle = partsRefs[cfg.index].current.querySelector('.circle-rate-progress')
        if (rateEle) {
          rateEles.push(rateEle)
        }
      }
    })
    new ScrollMagic.Scene({ triggerElement: pagesRef[2].current, duration: '50%' })
      .setTween(explodeTimeline)
      .addTo(controller);
    new ScrollMagic.Scene({
      triggerElement: pagesRef[2].current,
      duration: '200%',
      triggerHook: 'onLeave',
    }).setPin(partsPageRef.current, { pushFollowers: false })
      .addTo(controller);
    partsTimeline.to(partsPageRef.current.querySelector('.title-1'), {
      opacity: 0,
      y: '100%',
      duration: 1,
    }, 0)
    partsTimeline.to(partsPageRef.current.querySelector('.title-2'), {
      opacity: 1,
      y: '0%',
      duration: 1,
    }, 0)
    new ScrollMagic.Scene({ triggerElement: pagesRef[3].current, duration: '50%' })
      .setTween(partsTimeline)
      .addTo(controller)
      .on("progress", (event) => {
        if (rateEles.length) {
          rateEles.forEach((ele) => {
            const e = new Event('progress')
            e.progress = event.progress
            ele.dispatchEvent(e)
          })
        }
      })
    new ScrollMagic.Scene({ triggerElement: pagesRef[4].current, duration: '50%' })
      .setTween(trashRef.current, 1, { x: '-20%', y: '-20%' })
      .addTo(controller)
      .on('progress', event => {
        explodeTimeline.seek(1 * (1 - event.progress))
        partsTimeline.seek(1 * (1 - event.progress))
      })
  }, [data])

  const n = `#${String(data.id).padStart(3, '0')}`
  const parts = useMemo(() => {
    if (!data) return null
    return data.imgs.map(({ src, centeroid, x, width, partName, side }, i) => {
      let pos
      let linePos
      if (partName) {
        pos = side ? (x * 1 + width * 1) / imgSize[0] * 100 + 3 : (1 - x / imgSize[0]) * 100 + 3
        linePos = side ? {
          left: `${pos}%`,
          right: `${100 - pos}%`
        } : {
          right: `${pos}%`,
          left: `${100 - pos}%`
        }
      }
      const top = `${centeroid[1] / imgSize[1] * 100}%`
      return (
        <Box.FullAbs ref={layerRefs[i]} key={i}>
          <BackgroundImage ref={animaRefs[i]} ratio={imgSize[0] / imgSize[1]} src={src} />
          {partName && (
            <Box ref={partsRefs[i]}>
              {/* <Box.Absolute
                top={top}
                left={`${left}%`}
                width="0.5em"
                height="0.5em"
                transform="translate(-50%, -50%)"
                bg="black"
              /> */}
              <Box.Absolute
                top={top}
                style={linePos}
                height="2px"
                bg={colorScheme}
                className="line"
              />
              <Box.Absolute
                top={top}
                style={{ [side ? 'left' : 'right'] : `${pos}%` }}
                transform="translateY(-50%)"
                whiteSpace="nowrap"
                className="circle-container"
                >
                <Circle
                  bg={colorScheme}
                  width="12em"
                  textAlign="center"
                  className="circle-1"
                  opacity={0}
                >
                  <Text color="black" fontSize="1.5em" fontWeight="900">{partName}</Text>
                  <Text color="white" fontSize="1.25em">{data.partsDetail[partName]}</Text>
                </Circle>
                <Box.FullAbs className="circle-2" transform="scale(0)">
                  <Circle border="2px solid" borderColor={colorScheme} bg="white" width="100%" textAlign="center">
                    <Text color="black" fontSize="1.5em" fontWeight="900">{data.belongsTo[partName]}</Text>
                    {data.recycleRate[data.belongsTo[partName]] && (
                      <Text color={colorScheme} fontSize="1.25em" fontWeight="900">回收率{data.recycleRate[data.belongsTo[partName]]}%</Text>
                    )}
                  </Circle>
                </Box.FullAbs>
                <Box.Absolute className="circle-rate" opacity="0" left="-8%" right="-8%" top="-8%" bottom="-8%">
                  {data.recycleRate[data.belongsTo[partName]] && (
                    <RateCircle className="circle-rate-progress" value={data.recycleRate[data.belongsTo[partName]]} color={colorScheme} />
                  )}
                </Box.Absolute>
              </Box.Absolute>
            </Box>
          )}
        </Box.FullAbs>
      )
    })
  }, [data])

  const pages = [
    <>
      <Box.Absolute left="1em" top="1em">
        <Text.Number
          textStroke="3px"
          textStrokeColor={`colors.${colorScheme}`}
          color="white"
          fontSize="10em"
        >{n}</Text.Number>
      </Box.Absolute>
      <Box.AbsCenter width="100%" textAlign="center" transform="rotate(-12deg)">
        <Text as="h2" color={colorScheme} fontSize={getNameSize(data.name)} fontWeight="900">
          {data.name}
        </Text>
      </Box.AbsCenter>
      <Box.Absolute bottom="10%" right="4em" width="33%">
        <Text color={colorScheme} textAlign="justify">
          {data.description}
        </Text>
      </Box.Absolute>
      <Box.Absolute left="50%" bottom="1em" width="4em" role="button" transform="translateX(-50%)">
        <ChevDown />
      </Box.Absolute>
    </>,
    <>
      <TrashName>{data.name}</TrashName>
      <Box.Absolute top="10%" width="80%" left="5%">
        <Hashtag color={colorScheme}>{data.hashtag}</Hashtag>
      </Box.Absolute>
      <Box.Absolute bottom="10%" right="4em" width="33%">
        <Text color="white" textAlign="justify">
          {data.description}
        </Text>
      </Box.Absolute>
      <TrashNumber>{n}</TrashNumber>
      <Box.Absolute left="50%" bottom="1em" width="4em" role="button" transform="translateX(-50%)">
        <ChevDown />
      </Box.Absolute>
    </>,
    <Box ref={partsPageRef} height="100%">
      <TrashName color={colorScheme}>{data.name}</TrashName>
      <SectionTitle className="title-1">組成的材質是什麼？</SectionTitle>
      <SectionTitle className="title-2" opacity="0" transform="translateY(-100%)">用完應該丟在哪裡？</SectionTitle>
      <TrashNumber color={colorScheme}>{n}</TrashNumber>
    </Box>,
    <>
    </>,
    <>
    <Box width="75%" mx="auto">
      <Box pt="5%" />
      <BackgroundImage src={shareBg} ratio={1368 / 746}>
        <Box.Absolute left="45%" top="40%" transform="trnsateY(-50%)" width="45%">
          <Text fontSize="2.25em" fontWeight="900">＃如果你不好好回收</Text>
          <Text fontSize="3.25em" fontWeight="900" color={colorScheme}>你就會夢到吸管插在鼻孔裡！</Text>
        </Box.Absolute>
        {pinEnd && (
          <Box.Absolute width="100%" left="0" top="0" transform="translate(-20%, -20%)">
            <AspectRatio ratio={imgSize[0] / imgSize[1]} overflow="visible">
              <Box overflow="visible">
                {data.imgs.map(img => (
                  <Box.FullAbs>
                    <BackgroundImage ratio={imgSize[0] / imgSize[1]} src={img.src} />
                  </Box.FullAbs>
                ))}
              </Box>
            </AspectRatio>
          </Box.Absolute>
        )}
      </BackgroundImage>
      <Text mt="0.5em" fontWeight="900" color="white" fontSize="1.5em" letterSpacing="0.1em">＃如果你想好好睡覺，你可以這麼做！</Text>
      <Handling steps={data.handling} />
    </Box>
    </>,
  ]
  const pageBg = ['', colorScheme, '', '', colorScheme]
  return (
    <>
      {pages.map((page, i) => (
        <FullpageHeight bg={pageBg[i]} key={i} ref={pagesRef[i]} overflowX="hidden" zIndex={1}>
          <Container position="relative" height="100%">
            {page}
            {i === 0 && (
              <Box
                ref={trashContainerRef}
                overflow="visible"
                top="0"
                transform="translateY(-50%)"
                zIndex="sticky"
              >
                <Box
                  ref={trashRef}
                  width="80%"
                  mx="auto"
                >
                  <AspectRatio ratio={imgSize[0] / imgSize[1]} overflow="visible">
                    <Box overflow="visible">
                      {parts}
                    </Box>
                  </AspectRatio>
                </Box>
              </Box>
            )}
          </Container>
        </FullpageHeight>
      ))}
      <Container py="2em">
        <Text fontSize="2em" fontWeight="900">猜你也丟過這些...</Text>
      </Container>
      <Box.Fixed top="5%" bottom="5%" right="1em" width="3em" zIndex="docked">
        <ScrollIndicator progress={scrollProgress} />
      </Box.Fixed>
    </>
  )
}

export default withData(TrashPage)
