import React from 'react'

import Flex from '../../../components/Flex'
import Circle from '../../../components/Circle'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Box from '../../../components/Box'

import next from './next.svg'
import crush from './crush.svg'
import peal from './peal.svg'
import seperate from './seperate.svg'
import teal from './teal.svg'
import bin from './bin.svg'
import wash from './wash.svg'

const icons = {
  沖洗: wash,
  撕除封膜: peal,
  丟進回收桶: bin,
  分解元件: seperate,
  壓扁瓶身: crush,
  撕除膠帶: teal,
}

const Handling = ({ steps, ...props }) => {
  return (
    <Flex alignItems="center" my="1em" {...props}>
      {[...steps, '丟進回收桶'].reduce((all, step, i) => {
        const s = [
          <Box width="10%" key={`icon-${i}`}>
            <Circle width="100%" border="2px solid white">
              <Box px="5%">
                <Image src={icons[step]} />
              </Box>
            </Circle>
          </Box>,
          <Box color="white" px="1em" key={`text-${i}`}>
            <Text fontSize="1.5em" fontWeight="500" letterSpacing="0.1em">{step}</Text>
          </Box>
        ]
        return [...all, i > 0 && <Box key={`step-${i}`} width="12%" px="1%"><Image src={next} /></Box>, ...s]
      }, [])}
    </Flex>
  )
}

export default Handling
