import { groupBy, reduce } from 'lodash'
import { useMemo } from 'react';

import useData from './useData';
import images from './images'

const withData = SubComp => props => {
  const { match: { params: { id } } } = props
  const allData = useData()
  const data = useMemo(() => {
    if (!allData) return null
    const index = id * 1
    const ordered = allData[index].parts.map((cfg, index) => ({
      ...cfg,
      index,
      order: cfg.order - 1,
      centeroid: [
        cfg.x * 1 + cfg.width / 2,
        cfg.y * 1 + cfg.height / 2,
      ],
    }))
    const positions = []
    const range = ordered.reduce((all, cfg) => {
      const top = cfg.y * 1
      const bottom = top + cfg.height * 1
      return [Math.min(all[0], top), Math.max(all[1], bottom)]
    }, [Infinity, 0])
    const centeroid = range[0] + range[1] / 2
    const totalHeight = reduce(groupBy(ordered, 'order'), (total, orderEle, order) => {
      const y = total + orderEle[0].height * 1
      positions[order] = y
      return y
    }, 0)
    let partCount = 0

    return {
      ...allData[index],
      totalHeight,
      centeroid,
      positions,
      imgs: ordered.map(o => {
        if (o.partName) {
          partCount += 1
          o.side = partCount % 2
        }
        return {
          ...o,
          src: images[allData[index].name][o.layerName],
        }
      }),
    }
  }, [allData, id])
  return data ? <SubComp {...props} data={data} /> : null
}

export default withData
