import { useEffect, useState } from 'react'
import { tsv } from 'd3-fetch'
import { fromPairs, groupBy, mapKeys, mapValues } from 'lodash'

import cfgSource from './cfg.tsv'
import dataSource from './data.tsv'

// https://docs.google.com/spreadsheets/d/1TlGNBNi3JHkuuyql7ruVbFI6bfIxyvHC3AWZ0HrMWhE/edit#gid=0
const cfgKeys = {
  '垃圾名稱': 'name',
  '拆解排序': 'order',
  '圖層H高度': 'height',
  '圖層W寬度': 'width',
  '圖層Y位置': 'y',
  '圖層X位置': 'x',
  '圖層名稱': 'layerName',
  '對應部分/材質': 'partName',
}

// https://docs.google.com/spreadsheets/d/1qx-om_yU_8cIwDyp5pliiixTXiyhDV9Nok4Fd3h7be8/edit#gid=0
const dataKeys = {
  '品項': 'name',
  '可分解為哪些部分/材質': 'partsDetail',
  '同義詞': 'synonym',
  '回收前使用者應做什麼處理': 'handling',
  '處理後應該丟哪一類?': 'belongsTo',
  '使用場域': 'places',
  '各縣市回收狀況': 'recyclable',
  '回收率': 'recycleRate',
  '資源化比例': 'resourceRate',
  '特殊標籤': 'hashtag',
  '文案': 'description',
}

const lineParser = d => {
  const lines = d.replace(/\n|、\n/g, '\n').split('\n')
  return fromPairs(lines.map(l => l.split('-')))
}

const rateParser = d => {
  const lines = d.split('\n')
  return fromPairs(lines.map(l => {
    const splitted = l.split('：')
    return [splitted[0], Math.round(parseFloat(splitted[1]))]
  }))
}

const transformer = {
  partsDetail: lineParser,
  belongsTo: lineParser,
  recycleRate: rateParser,
  resourceRate: rateParser,
  handling: d => d.split('、'),
}

const remapKeys = (data, keyMap) => data.map((dd, id) => mapValues({
  id,
  ...mapKeys(dd, (v, k) => keyMap[k] || k),
}, (v, k) => transformer[k] ? transformer[k](v) : v))

const useData = () => {
  const [data, setData] = useState()
  useEffect(() => {
    Promise.all([
      tsv(dataSource),
      tsv(cfgSource),
    ])
    .then(([dd, cfg]) => {
      const grouped = groupBy(remapKeys(cfg, cfgKeys), 'name')
      const transformed = remapKeys(dd, dataKeys).map(d => ({ ...d, parts: grouped[d.name] }))
      return setData(transformed)
    })
  }, [])
  return data
}

export default useData
